  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(14px); /* Adjust the distance and direction of the movement */
  }
  75% {
    transform: translateY(8px); /* Adjust the distance and direction of the movement */
  }
  100% {
    transform: translateY(0);
  }
}

